import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PaymentCategoryCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/payment/paymentCategory/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/payment-categories"

class CreatePaymentCategory extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createPaymentCategory } = this.props;
    createPaymentCategory();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.paymentCategory.error) {
      notification("error", this.props.paymentCategory.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.paymentCategory.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { storePaymentCategory } = this.props;

    // Uppercasing paymentCategory code
    // value.payment_category_code = value.payment_category_code.toUpperCase();
    // console.log(value);
    storePaymentCategory(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="paymentCategories.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <PaymentCategoryCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            paymentCategory={this.props.paymentCategory}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    paymentCategory: state.paymentCategory
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreatePaymentCategory);
